.main-dashboard {
    background-color: #F9F9FB;
}

.grid-container {
    display: grid;
    grid-template-columns: auto auto auto;
    padding: 10px;
  }

  .sidebar {
    margin: 0;
    padding: 0;
    width: 220px;
    background-color: #222222;
    position: fixed;
    height: 100%;
    overflow: auto;
  }
  
  .sidebar-head {
    background-color: #33CABB;
    margin: 15px;
    padding: 10px;
    border-radius: 25px;
    text-align: center;
  }

  .sidebar a {
    display: block;
    color: #bbb;
    padding: 16px;
    text-decoration: none;
  }
   
  .sidebar a.active {
    background-color: #555;
    color: white;
  }
  
  .sidebar a:hover:not(.active) {
    background-color: #555;
    color: white;
  }

  div.content {
    margin-left: 220px;
    padding: 1px 16px;
  }
  
  @media screen and (max-width: 1100px) {
    .sidebar {
      width: 100%;
      height: auto;
      position: relative;
    }
    .sidebar a {float: left;}
    div.content {margin-left: 0;}
    
.grid-container {
    display: flex;
    flex-wrap: wrap;
  }
}
  
  @media screen and (max-width: 450px) {
    .sidebar a {
      text-align: center;
      float: none;
    }
    
.grid-container {
    display: flex;
    flex-wrap: wrap;
  }
}

.search{
    flex:1;
    margin-right: 30px;
    margin-left: 30px;
    display:flex;
    padding: 10px;
}
.search:before{
    content:'';
    width:30px;
    background-color: #FFFFFF;
}
.search-input {
    flex:1;
    background-color: #FFFFFF;
    border: 0px;
    background-image: url('../assets/search.jpeg');
    background-position: 10px 10px; 
    background-repeat: no-repeat;
    padding: 12px 20px 12px 40px;
    -webkit-transition: width 0.4s ease-in-out;
    transition: width 0.4s ease-in-out;
}
  
.filter {
  padding-left: 40px;
  padding-right: 40px;
}

.dashboard-row {
  position: relative;
  width: 100%;
}